<template>
    <div class="section-nav-container" :class="[navIsOpen ? 'nav-is-open' : '']">
        <div class="overlay"></div>
        <div class="centerer">
            <ul v-if="sections" class="section-nav">
                <li :class="[item.id === currentSection.id ? 'current' : '']" @click="navigate(item)" v-for="(item, index) in sections" :key="index">
                    <span class="dot"></span>
                    <span class="text" v-html="item.title"></span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import _ from 'lodash';
    import $ from 'jquery';
    import 'jquery.easing';

    export default {
        name: "section-nav",
        props: ['sections'],
        data() {
            return {
                navIsOpen: false,

                scrollDuration: 600,

                lastScrollPos: 0,
                scrollDir: 'down',

                isScrolling: false,
                reactToScrolls: true,
                didWheel: false,

                currentSectionIndex: 0,

                windowWidth: 0,
                windowHeight: 0,
            }
        },
        computed: {
            currentSection() {
                return this.sections[this.currentSectionIndex];
            },
            scrollJackingEnabled() {
                return this.windowWidth > 1300 && this.windowHeight > 800;
            }
        },
        mounted() {


            document.addEventListener('click', e => {
                if(e.target.matches('[data-termine-cta]')) {
                    this.scrollTo(document.querySelector('#sectionTermine'));
                    e.preventDefault();
                }
            })




            const setWindowSize = _.debounce(() => {
                this.windowWidth = window.innerWidth;
                this.windowHeight = window.innerHeight;
            }, 100);

            window.addEventListener('resize', setWindowSize)
            setWindowSize();

            const observer = new IntersectionObserver((entries) => {
                for(const entry of entries) {

                    let highestIndex = 0;
                    for(const sectionIndex in this.sections) {
                        if(entry.target === this.sections[sectionIndex].el) {
                            this.sections[sectionIndex].intersectionRatio = entry.intersectionRatio;
                        }

                        if(this.sections[sectionIndex].intersectionRatio > this.sections[highestIndex].intersectionRatio) {
                            highestIndex = sectionIndex;
                        }
                    }

                    this.currentSectionIndex = highestIndex;
                }
            }, { threshold: [.1, .5, .9] });

            for(const section of this.sections) {
                observer.observe(section.el);
            }





            const setNaviToHidden = _.debounce(() => {
                this.navIsOpen = false;
            }, 100)

            this.$el.addEventListener('mouseover', () => {
                setNaviToHidden.cancel();
                this.navIsOpen = true;
            })

            this.$el.addEventListener('mouseout', () => {
                setNaviToHidden();
            });
            this.$el.addEventListener('click', (e) => {
                e.preventDefault();
                e.stopImmediatePropagation();
                setNaviToHidden();
            });



            window.debounceFn = _.debounce(() => {
                this.reactToScrolls = true;
            }, 20);

            window.addEventListener('wheel', (e) => {
                if(!this.scrollJackingEnabled) return;
                e.preventDefault();

                if(this.isScrolling) {
                    return;
                }

                if(!this.reactToScrolls) {
                    return debounceFn();
                }

                // const newScrollDir = window.pageYOffset >= this.lastScrollPos  ? 'down' : 'up';
                const newScrollDir = e.deltaY > 0 ? 'down' : 'up';
                this.scrollDir = newScrollDir;
                this.lastScrollPos = window.pageYOffset;

                const nextSectionIndex = this.resolveNextSectionIndex(this.scrollDir);
                const nextSection = this.sections[nextSectionIndex];

                if(nextSection) {
                    this.scrollTo(nextSection.el);
                }

            }, {
                passive: false,
                capture: true
            });
        },
        methods: {
            navigate(section) {
                this.scrollTo(section.el);
            },
            resolveNextSectionIndex(direction) {
                const i = parseInt(this.currentSectionIndex);
                if(direction === 'down') {
                    return this.sections.hasOwnProperty(i + 1) ? i + 1 : null;
                } else {
                    return this.sections.hasOwnProperty(i - 1) ? i - 1 : null;
                }
            },
            scrollTo(el) {
                if(el) {
                    this.isScrolling = true;
                    this.reactToScrolls = false;

                    $('html, body').stop(true, false).animate({
                        scrollTop: el.offsetTop
                    }, this.scrollDuration, 'easeInOutCubic', () => {
                        setTimeout(() => {
                            this.lastScrollPos = el.offsetTop;
                            this.isScrolling = false;
                            debounceFn();
                        }, 10)
                    });
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

    $lightblue: #ECF3F7;


    .section-nav-container {
        display: none;
    }

    @media (min-width: 1200px) {
        .overlay {
            position: fixed;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 9;
            background: rgba(255,255,255,.9);
            transition-duration: .5s;

            opacity: 0;
            pointer-events: none;

            .nav-is-open & {
                opacity: 1;
            }
        }

        .section-nav-container {
            display: block;
        }
    }

    .centerer {
        position: fixed;
        top: 0;
        left: 100%;
        bottom: 0;
        z-index: 10;
        display: flex;
        overflow: visible;
        flex-direction: column;
        justify-content: center;
    }

    .section-nav {
        position: relative;
        right: 28px;
        display: flex;
        flex-direction: column;
        width: 28px;
        margin: 0;
        padding: 0;

        @media (max-width: 1200px) {
            right: 15px;
        }


        @media (min-width: 1200px) {
            .nav-is-open & {
                .text {
                    pointer-events: auto;
                    opacity: .6;
                    transform: none;
                }
            }
        }

        li {
            position: relative;
            display: block;
            cursor: pointer;
            white-space: nowrap;
            transition-duration: .5s;
            margin: 0 0 2px;
            padding: 6px 0;

            &:hover {
                .text {
                    transform: translateX(10px);
                    transition-duration: .1s;
                    opacity: 1;
                }
            }

            &.current, &:hover {
                .dot {
                    transform: scale(1.5);
                    transition-duration: .1s;
                }
            }

            .dot {
                display: block;
                height: 10px;
                width: 10px;
                background: $lightblue;
                box-shadow: 0 0 0 0 rgba(255,255,255,1), 0 0 0 2px #FFF;
                border-radius: 100%;
                transition-duration: .8s;
            }

            .text {
                position: absolute;
                top: 2px;
                right: calc(100% + 20px);
                padding: 3px 8px;
                line-height: 1;
                border-radius: 100px;
                pointer-events: none;
                opacity: 0;
                transition-duration: .8s;
                color: #333;
                font-size: 14px;
                background: rgba(0,0,0,0);

                &::after {
                    content: '';
                    position: absolute;
                    top: -2px;
                    bottom: -2px;
                    right: -20px;
                    left: -20px;
                }
            }
        }
    }
</style>
