<template>
    <div :id="section.id" class="section section-termine bg-blue">
        <div class="container">
            <div class="relative">
                <h2 class="h1 text-center">Termine</h2>

                <div class="termine-box">
                    <div class="row header-row only-desktop">
                        <div class="cell cell-city">Ort</div>
                        <div class="cell cell-date">Datum</div>
                        <div class="cell cell-sem-nr">Seminar-Nr.</div>
                        <div class="cell cell-actions">&nbsp;</div>
                    </div>
                    <div class="row" v-for="course in courses">
                        <div class="cell cell-city">{{ course.location }}</div>
                        <div class="cell cell-date">{{ course.date }}</div>
                        <div class="cell cell-sem-nr">{{ course.seminarNumber }}</div>
                        <div class="cell cell-actions"><a :href="course.ctaUrl" target="_blank" class="button2 --outlined">Zum Seminar</a></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import slugify from 'slugify';
    import mockData from '../../course-api-data-mock';

    export default {
        name: "section-termine",
        props: ['section'],
        data() {
            return {
                courses: null
            }
        },
        created() {
            const handleData = (data) => {
                console.log(data.apiData.response);
                let courseData = [];
                for(const course of data.apiData.response) {
                    const fromDate = new Date(course.von);
                    const toDate = new Date(course.bis);

                    const dateString = (fromDate.getMonth() + 1) + '.' +fromDate.getDate() + '. – ' + (toDate.getMonth() + 1) + '.' +toDate.getDate() + '.' + toDate.getFullYear();

                    const slug = slugify(course.hotel.ortB).toLowerCase();
                    const url = 'https://www.ifb.de/betriebsrat/seminare/termin/'+ slug +'-' + course.optionen.terminId;

                    courseData.push({
                        date: dateString,
                        location: course.hotel.ortB,
                        seminarNumber: course.buchungsNummer,
                        ctaUrl: url
                    });
                }

                this.courses = courseData;
            }

            handleData(mockData);
        }
    }
</script>

