                    <h2 class="h1">So laufen die Impulstage ab:</h2>
                    <p>Es gibt keine festgelegten Themenschwerpunkte, kein reines Theoriewissen. Sie bringen Ihre Themen und Probleme mit: egal ob Ihre Rolle als BRV betreffend, innerhalb des Gremiums, Erwartungen der Mitarbeiter oder der Zusammenarbeit mit dem Arbeitgeber. Gemeinsam mit anderen Betriebsratsvorsitzenden und unserem ausgewählten Expertenteam, bestehend aus einem Juristen und einem Kommunikationstrainer, finden Sie passende Lösungsansätze, die Ihnen wirklich etwas bringen.</p>
                </div>

                <div class="slider-outer-container">
                    <div class="slider-header">
                        <span class="text" :class="[!showSliderText ? 'hide' : '']">
                            {{ sliderText }}
                        </span>
                    </div>
                    <div class="slider-container">
                        <div class="slide" data-label="Tag 2">
                            <img src="@/assets/03:04_Tagesplanung/slide2.jpg" alt="">
                        </div>
                        <div class="slide" data-label="Tag 1">
