<script>

    import $ from 'jquery';

    //import 'slick-carousel';
    import 'slick-carousel/slick/slick.min'
    import 'slick-carousel/slick/slick.css'

    export default {
        name: "section-tagesplanung",
        props: ['section'],
        data() {
            return {
                sliderText: null,
                showSliderText: true
            }
        },
        mounted() {
            const slickElem = $('.slider-container');

            slickElem.on('beforeChange', () => {
                this.showSliderText = false;
            });

            slickElem.on('afterChange', (slick, currentSlide, nextSlide) => {
                const el = slickElem[0].querySelector('[data-slick-index="'+ nextSlide +'"]');
                this.sliderText = el.dataset.label;
                this.showSliderText = true;
            });
            slickElem.on('init', () => {
                const el = slickElem[0].querySelector('[data-slick-index="'+ 0 +'"]');
                this.sliderText = el.dataset.label;
            });

            slickElem.slick({
                slidesToShow: 1,
                arrows: true,
                dots: false,
                rows: 0,
                prevArrow: '<i class="arrow-left nav-arrow"></i>',
                nextArrow: '<i class="arrow-right nav-arrow"></i>',
            });
        }
    }
</script>

