
const init = (el) => {

    const setDebugMode = (bool) => {
        el.classList.toggle('debug-mode', bool);
    }

    if(localStorage.getItem('debugMode')) {
        try {
            const mode = JSON.parse(localStorage.getItem('debugMode'));
            setDebugMode(mode);
        } catch(e) {}
    }

    document.addEventListener('dblclick', () => {
        const curr = el.classList.contains('debug-mode');
        const newMode = !curr;
        setDebugMode(newMode);
        localStorage.setItem('debugMode', JSON.stringify(newMode));
    });
}

export default init;